
import React from "react";
import { connect } from 'react-redux'
import {
  Button,
  Card,
  CardBody,
  FormGroup,
  Form,
  Input,
  Row,
  Col
} from "reactstrap";

import { changeFormAnswer } from '../../actionInitial'


let form = [
  {
    "name": "Name",
    "placeholder": "Name",
    "type": "text"
  },
  {
    "name": "Gender",
    "placeholder": "Gender",
    "type": "text"
  },
  {
    "name": "DOB",
    "placeholder": "dd/mm/yyyy",
    "type": "text"
  },
  {
    "name": "Occupation type",
    "placeholder": "Bussiness / Employed / Free Lancer / Partner",
    "type": "text"
  },
  {
    "name": "Occupation Designation",
    "placeholder": "Occupation Designation",
    "type": "text"
  },
  {
    "name": "Family",
    "placeholder": "Married / Unmarried",
    "type": "text"
  },
  {
    "name": "Family Type",
    "placeholder": "Joint / Nuclear ",
    "type": "text"
  },
  {
    "name": "Phone number",
    "placeholder": "Phone number",
    "type": "number"
  },
  {
    "name": "Email Id",
    "placeholder": "Email Id",
    "type": "text"
  },
  {
    "name": "Address",
    "placeholder": "Landmark, Street, Town, City, State, Country",
    "type": "text"
  },
  {
    "name": "Senior Citizen",
    "placeholder": "How many Senior Citizens?",
    "type": "text"
  },
  {
    "name": "Children",
    "placeholder": "How many Children?",
    "type": "text"
  },
  {
    "name": "Annual Income",
    "placeholder": "Annual Income",
    "type": "number"
  },
]

function formParser(form) {
  return form.map(field => {
    return {
      ...field,
      "key": field.name.replace(" ", "")
    }
  })
}

let form1 = formParser(form)

class Login extends React.Component {

  componentWillMount() {
    form1.map(field => {
      this.props.changeFormAnswer(field.key, "")
    })
  }
  render() {
    return (
      <>
        <Col lg="5" md="7">
          <Card className="bg-secondary shadow border-0">
            <CardBody className="px-lg-5 py-lg-5">
              <Form role="form">
                {
                  form1.map(obj => {
                    return (
                      <Row>
                        <Col>
                          <FormGroup>
                            <label
                              className="form-control-label"
                              htmlFor="input-first-name"
                            >
                              {obj.name}
                            </label>
                            <Input
                              className="form-control-alternative"
                              onChange={async (event) => {
                                this.props.changeFormAnswer(obj.key, event.target.value)
                              }}
                              placeholder={obj.placeholder}
                              type={obj.type}
                            />
                          </FormGroup>
                        </Col>
                      </Row>
                    )
                  })
                }


                <div className="text-center">
                  <Button onClick={() => {
                    this.props.history.push("/auth/select")
                  }} className="my-4" color={"primary"} type="button">
                    Continue
                  </Button>
                </div>
              </Form>
            </CardBody>
          </Card>

        </Col>
      </>
    );
  }
}

const mapStateToProps = ({ formAnswers }) => {
  return {
    formAnswers
  }
}

export default connect(mapStateToProps, { changeFormAnswer })(Login);
