class Profile extends React.Component {


 constructor(props) {
  super(props)
  this.formulaKey = this.getFormulaKey()
  this.formulas = this.getFormulaForFields()
 }

 getFieldsForKey = () => {
  return forms[this.formulaKey].fields
 }

 componentWillMount() {
  // Object.keys(this.formulas).map(async key => {
  //  await this.props.changeAnswer(this.formulaKey, key, this.formulas[key].initialValue)
  // })
 }

 getAllVariables = (obj) => {
  if (obj.formula.includes("loop")) {
   let c = obj.formula.replace("loop(", "").split("")
   let args = c.slice(0, c.length - 1).join("").split(",")
   return new Formula(args[0]).getVariables()
  }

  return new Formula(obj.formula).getVariables()
 }

 calculateAssociatesForFormulas = (formulas) => {
  Object.keys(formulas).forEach(formulaName => {
   if (formulas[formulaName].type == "formula") {
    let variables = this.getAllVariables(formulas[formulaName])
    variables.forEach(variable => {
     if (formulas[variable].hasOwnProperty("associates")) {
      if (!formulas[variable].associates.includes(formulaName))
       formulas[variable].associates.push(formulaName)
     }
     else {
      formulas[variable]["associates"] = [formulaName]
     }
    })
   }
  })
  return formulas
 }

 getFormulaForFields = () => {
  let obj = {}
  this.getFieldsForKey().map(field => {
   if (FORMULAS.hasOwnProperty(field)) {
    obj[field] = {
     ...FORMULAS[field]
    }
   }
  })
  return obj
 }

 getFormulaKey = (props) => {
  return this.props.location.pathname.split("/")[2]
 }




 loop = (formulaString, storageVariable, loopVariable, summingValue) => {
  let sVal = this.props.answers[this.formulaKey][storageVariable]
  let loops = this.props.answers[this.formulaKey][loopVariable]
  if (loops < 0)
   return 0
  let sum = 0
  while (loops--) {
   if (summingValue != null) {
    sum += sVal
   }
   sVal = new Formula(formulaString).evaluate({
    ...this.spreadVariables(new Formula(formulaString)),
    [storageVariable]: sVal
   })

  }
  if (summingValue != null) {
   sum += sVal
   return sum
  }
  return sVal
 }

 spreadVariables = (formulaObject) => {
  let obj = {}
  formulaObject.getVariables().forEach((variable) => {
   obj[variable] = this.props.answers[this.formulaKey][variable]
  })
  return obj
 }

 recalculateAssociates = async (variableName) => {
  this.formulas[variableName].associates.forEach(async (associate) => {
   await this.props.changeAnswer(this.formulaKey, associate, this.getValueFromFormulaObject(associate))
   this.recalculateAssociates(associate)
  })
 }

 getFormulaObject = (formulaName) => {
  let formulaObject = new Formula(this.formulas[formulaName].formula)
  return formulaObject
 }

 getValueFromFormulaObject = (formulaName) => {
  if (this.formulas[formulaName].type == "formula")
   if (this.formulas[formulaName].formula.includes("loop")) {
    let c = this.formulas[formulaName].formula.replace("loop(", "").split("")
    let args = c.slice(0, c.length - 1).join("").split(",")
    if (args.length > 3) {
     return Math.round(this.loop(args[0], args[1], args[2], args[3]))
    }
    return Math.round(this.loop(args[0], args[1], args[2], null))
   }
  let formulaObject = this.getFormulaObject(formulaName)
  return (formulaObject.evaluate(this.spreadVariables(formulaObject)))
 }


 render() {

  return (
   <>
    <CalcHeader />
    {/* Page content */}
    <Container className="mt--9" fluid>
     <Row>
      <Col >
       <Card className="bg-secondary shadow">

        <CardBody>
         <Form>
          <h6 className="heading-small text-muted mb-4">
           Fill in the details {this.props.location.pathname}
          </h6>
          <div className="pl-lg-4">
           <Row>
            {
             Object.keys(this.formulas).map(key => {
              if (this.formulas[key].type == "variable") {
               return (
                <Col lg="6">
                 <FormGroup>
                  <label
                   className="form-control-label"
                   htmlFor="input-first-name"
                  >
                   {this.formulas[key].displayName}
                  </label>
                  <Input
                   className="form-control-alternative"
                   // defaultValue={this.props.answers[this.formulaKey][key]}
                   onChange={async (event) => {
                    await this.props.changeAnswer(this.formulaKey, key, event.target.value)
                    this.recalculateAssociates(key)
                   }}
                   value={this.props.answers[this.formulaKey][key]}
                   placeholder={key}
                   type={this.formulas[key].valueType}
                  />
                 </FormGroup>
                </Col>
               )
              }
              else if (this.formulas[key].type == "formula") {
               return (
                <Col lg="6">
                 <FormGroup>
                  <label
                   className="form-control-label"
                   htmlFor="input-first-name"
                  >
                   {this.formulas[key].displayName}
                  </label>
                  <Input
                   className="form-control-alternative"
                   disabled
                   defaultValue={0}
                   value={Math.round(this.props.answers[this.formulaKey][key])}
                   placeholder={key}
                   type={this.formulas[key].valueType}
                  />
                 </FormGroup>
                </Col>
               )
              }
             })
            }
           </Row>
          </div>
         </Form>
        </CardBody>
       </Card>
      </Col>
     </Row>
    </Container>
   </>
  );
 }
}



