
import React from "react";
import { connect } from 'react-redux'
import {
 Button,
 Card,
 CardBody,
 FormGroup,
 Form,
 Input,
 Row,
 Col
} from "reactstrap";

import { selectForms } from '../../actionSelect'
import { changeAnswer } from '../../action'

import forms from './forms.json'
import FORMULAS from './formulas.json'


class Login extends React.Component {


 getFieldsForKey = (formulaKey) => {
  return forms[formulaKey].fields
 }

 getFormulaForFields = (formulaKey) => {
  let obj = {}
  this.getFieldsForKey(formulaKey).map(field => {
   if (FORMULAS.hasOwnProperty(field)) {
    obj[field] = {
     ...FORMULAS[field]
    }
   }
  })
  return obj
 }


 render() {
  return (
   <>
    <Col lg="5" md="7">
     <Card className="bg-secondary shadow border-0">
      <CardBody className="px-lg-5 py-lg-5">
       <Form role="form">
        <Row>
         {
          Object.keys(forms).map(key => {
           return (

            <Col lg="6">
             <Button onClick={() => {
              this.props.selectForms(key)
             }} className="my-3" color={this.props.selectedForms.includes(key) ? "success" : "secondary"} type="button">
              {forms[key].name}
             </Button>
            </Col>

           )
          })
         }
        </Row>


        {
         this.props.selectedForms.length > 0 ? (
          <div className="text-center">
           <Button onClick={() => {
            this.props.selectedForms.map(formulaKey => {
             let formulas = this.getFormulaForFields(formulaKey)
             Object.keys(formulas).map(async key => {
              await this.props.changeAnswer(formulaKey, key, formulas[key].initialValue)
             })
            })

            this.props.history.push("/dashboard/" + this.props.selectedForms[0])
           }} className="my-4" color="primary" type="button">
            Continue
         </Button>
          </div>
         ) : (null)
        }


       </Form>
      </CardBody>
     </Card>

    </Col>
   </>
  );
 }
}

const mapStateToProps = ({ selectedForms }) => {
 return {
  selectedForms
 }
}

export default connect(mapStateToProps, { selectForms, changeAnswer })(Login);
