import { combineReducers } from 'redux';

// import Reactotron from 'reactotron-react-native'
const answers = (state = {}, action) => {
 let dummyState
 if (action.header != "answer")
  return { ...state }

 // if (action.type == "initialiseeeee") {
 //  dummyState = { ...state }
 //  Object.keys(formulas).map(async key => {
 //   if (dummyState.hasOwnProperty(action.key)) {
 //    dummyState[action.key] = { ...dummyState[action.key], [key]: formulas[key].initialValue }
 //   }
 //   else {
 //    dummyState[action.key] = { [key]: formulas[key].initialValue }
 //   }
 //  })
 //  return { ...dummyState }
 // }


 if (state.hasOwnProperty(action.key)) {
  dummyState = { ...state }
  dummyState[action.key] = { ...dummyState[action.key], [action.type]: action.payload }
  return { ...dummyState }
 }
 else {
  dummyState = { ...state }
  dummyState[action.key] = { [action.type]: action.payload }
  return { ...dummyState }
 }
}

const formAnswers = (state = {}, action) => {
 if (action.header != "initial")
  return { ...state }
 return { ...state, [action.type]: action.payload }
}

const selectedForms = (state = [], action) => {
 if (action.type != "select")
  return [...state]
 if (state.includes(action.payload))
  return [...state].filter((val) => val != action.payload)
 let newState = [...state]
 newState.push(action.payload)
 return [...newState]
}

export default combineReducers({
 answers,
 formAnswers,
 selectedForms
});