class Profile extends React.Component {
 constructor(props) {
  super(props)
  this.state = {
   type: 'Marriage',
   presentvalue: 0,
   term: 0,
   inflation: 0,
   future: 0,
   rate: 0,
   annual: 0,
   monthly: 0,
  }
 }


 PMT = async () => {
  let term
  if (this.state.fulfilage > this.state.currentage) {
   term = this.state.fulfilage - this.state.currentage
  }
  else {
   term = 0
  }
  await this.setState({ term })


  let ppv = this.state.presentvalue, ir = this.state.inflation / 100, yrs = term, future = 0;
  // alert(JSON.stringify([ppv,ir,yrs]))
  if (ppv && ir && yrs) {
   while (yrs--) {
    ppv = ppv + (ppv * (ir))
   }
   future = Math.round(ppv)
  }
  else
   future = 0
  await this.setState({ future })

  let rate = this.state.rate / 100, nperiod = term, pv = 0, fv = future, type = 1;

  if (rate > 0 && nperiod > 0 && fv > 0) {
   if (!fv) fv = 0;
   if (!type) type = 0;

   if (rate == 0) return -(pv + fv) / nperiod;

   let pvif = Math.pow(1 + rate, nperiod);
   //let pvif = (1+rate)^nperiod
   let pmt = (rate / (pvif - 1)) * -(fv);

   if (type == 1) {
    pmt /= (1 + rate);
   };

   await this.setState({ annual: -Math.round(pmt) })
   await this.setState({ monthly: -Math.round(pmt / 12) })
  }
 }

 componentWillMount() {
  Object.keys(formulas).map(async key => {
   await this.props.changeAnswer(key, formulas[key].initialValue)
  })
 }

 loop = (formulaString, storageVariable, loopVariable, summingValue) => {
  let sVal = this.props.answers[storageVariable]
  let loops = this.props.answers[loopVariable]
  if (loops < 0)
   return 0
  let sum = 0
  while (loops--) {
   if (summingValue != null) {
    sum += sVal
   }
   sVal = new Formula(formulaString).evaluate({
    ...this.spreadVariables(new Formula(formulaString)),
    [storageVariable]: sVal
   })

  }
  if (summingValue != null) {
   sum += sVal
   return sum
  }
  return sVal
 }

 spreadVariables = (formulaObject) => {
  let obj = {}
  formulaObject.getVariables().forEach((variable) => {
   obj[variable] = this.props.answers[variable]
  })
  return obj
 }

 recalculateAssociates = async (variableName) => {
  formulas[variableName].associates.forEach(async (associate) => {
   await this.props.changeAnswer(associate, this.getValueFromFormulaObject(associate))
   this.recalculateAssociates(associate)
  })
 }

 getFormulaObject = (formulaName) => {
  let formulaObject = new Formula(formulas[formulaName].formula)
  return formulaObject
 }

 getValueFromFormulaObject = (formulaName) => {
  if (formulas[formulaName].type == "formula")
   if (formulas[formulaName].formula.includes("loop")) {
    let c = formulas[formulaName].formula.replace("loop(", "").split("")
    let args = c.slice(0, c.length - 1).join("").split(",")
    if (args.length > 3) {
     return Math.round(this.loop(args[0], args[1], args[2], args[3]))
    }
    return Math.round(this.loop(args[0], args[1], args[2], null))
   }
  let formulaObject = this.getFormulaObject(formulaName)
  return (formulaObject.evaluate(this.spreadVariables(formulaObject)))
 }

 render() {
  return (
   <>
    <CalcHeader />
    {/* Page content */}
    <Container className="mt--9" fluid>
     <Row>
      <Col >
       <Card className="bg-secondary shadow">

        <CardBody>
         <Form>
          <h6 className="heading-small text-muted mb-4">
           Fill in the details
                    </h6>
          <div className="pl-lg-4">
           <Row>
            {
             Object.keys(formulas).map(key => {
              if (formulas[key].type == "variable") {
               return (
                <Col lg="6">
                 <FormGroup>
                  <label
                   className="form-control-label"
                   htmlFor="input-first-name"
                  >
                   {key}
                  </label>
                  <Input
                   className="form-control-alternative"
                   defaultValue={this.props.answers[key]}
                   onChange={async (event) => {
                    await this.props.changeAnswer(key, event.target.value)
                    this.recalculateAssociates(key)
                   }}
                   placeholder={key}
                   type={formulas[key].valueType}
                  />
                 </FormGroup>
                </Col>
               )
              }
              else if (formulas[key].type == "formula") {
               return (
                <Col lg="6">
                 <FormGroup>
                  <label
                   className="form-control-label"
                   htmlFor="input-first-name"
                  >
                   {key}
                  </label>
                  <Input
                   className="form-control-alternative"
                   disabled
                   defaultValue={0}
                   value={Math.round(this.props.answers[key])}
                   placeholder={key}
                   type={formulas[key].valueType}
                  />
                 </FormGroup>
                </Col>
               )
              }
             })
            }
           </Row>
          </div>
         </Form>
        </CardBody>
       </Card>
      </Col>
     </Row>
    </Container>
   </>
  );
 }
}

