import React from "react";
import { Route, Switch } from "react-router-dom";
// reactstrap components
import { Container } from "reactstrap";
import { connect } from "react-redux"
// core components
import AdminNavbar from "components/Navbars/AdminNavbar.jsx";
import AdminFooter from "components/Footers/AdminFooter.jsx";
import Sidebar from "components/Sidebar/Sidebar.jsx";
import FormGen from "views/examples/FormGenerator.jsx"
import forms from 'views/examples/forms.json'


class Admin extends React.Component {
 componentDidUpdate(e) {
  document.documentElement.scrollTop = 0;
  document.scrollingElement.scrollTop = 0;
  this.refs.mainContent.scrollTop = 0;
 }

 routeGenerator = () => {
  return this.props.selectedForms.map((route) => {
   return {
    path: "/" + route,
    name: forms[route].name,
    icon: "ni ni-tv-2 text-primary",
    component: FormGen,
    layout: "/dashboard"
   }
  })
 }

 getRoutes = (routes) => {

  return routes.map((prop, key) => {
   if (prop.layout === "/dashboard") {
    return (
     <Route
      path={prop.layout + prop.path}
      component={prop.component}
      key={key}
     />
    );
   } else {
    return null;
   }
  });
 };
 getBrandText = path => {
  let routes = this.routeGenerator()
  for (let i = 0; i < routes.length; i++) {
   if (
    this.props.location.pathname.indexOf(
     routes[i].layout + routes[i].path
    ) !== -1
   ) {
    return routes[i].name;
   }
  }
  return "Brand";
 };
 render() {
  return (
   <>
    <Sidebar
     {...this.props}
     routes={this.routeGenerator()}
     logo={{
      innerLink: "/admin/index",
      imgSrc: require("assets/img/brand/argon-react.png"),
      imgAlt: "..."
     }}
    />
    <div className="main-content" ref="mainContent">
     <AdminNavbar
      {...this.props}
      brandText={this.getBrandText(this.props.location.pathname)}
     />
     <Switch>{this.getRoutes(this.routeGenerator())}</Switch>
     <Container fluid>
      <AdminFooter />
     </Container>
    </div>
   </>
  );
 }
}

const mapStateToProps = ({ selectedForms }) => {
 return {
  selectedForms
 }
}

export default connect(mapStateToProps)(Admin);
